.item__container {
  width: 100%;
  padding: 10px 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-bottom: 1px solid #ececec;
  background-color: #f5f5f5;
}

.item__container span {
  width: 15%;
  text-align: center;
  font-size: 0.9em;
}

.item__name {
  font-weight: bold;
}
.item__name::first-letter {
  text-transform: capitalize;
}

.item__amount {
  font-weight: bold;
  letter-spacing: 1px;
}

.item__id {
  color: #696969;
}
.item__date {
  color: #696969;
}

.item__income {
  background-color: #c0d0fc;
}

.item__income:hover {
  background-color: #d3deff;
  box-shadow: inset 1px 2px 6px #b8b8b8;
  cursor: pointer;
}

.item__expense {
  background-color: #ececec;
}

.item__expense:hover {
  background-color: #f3f3f3;
  box-shadow: inset 1px 2px 6px #b8b8b8;
  cursor: pointer;
}

.item__actions {
  width: 15%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.item__icons {
  width: 25px;
  cursor: pointer;
  transition: 0.2s;
}

.item__actions img {
  margin: 0 5px;
}

.item__icons:hover {
  transform: scale(1.1);
}

@media (max-width: 600px) {
  .item__container {
    padding: 0;
    height: 25vh;
    flex-wrap: wrap;
    flex-direction: column;
  }

  .item__container span {
    width: 100px;
    /* margin: 20px 0; */
  }

  .item__container-info > * {
    display: flex;
  }

  .item__actions {
    height: 70%;
    flex-direction: column;
    justify-content: space-evenly;
  }

  .item__expense {
    background-color: #dfdfdf;
  }
}
